import React from "react"
import { Button, Col, InputGroup } from "react-bootstrap";
import { Row } from "react-bootstrap";
import { ConConfig } from "../../Modelos/Config";
import Area from "../../Utiles/Charts/Area";
import { formatoFecha, formatoHora, fechaToNumber } from "../../Utiles/Formatos";
import { InputNumber } from "../../Utiles/Inputs";
type dolar = { fecha: string, valor: number };
export default class Dolar extends React.Component<{}, { dolares: dolar[], valor: number, fecha: string }>{
    constructor(p: {}) {
        super(p);
        this.state = { dolares: [], valor: 0, fecha: '' };
    }
    render() {
        const { valor, fecha } = this.state;
        return <>
            <Row>
                <InputGroup as={Col} md='8'>
                    <InputGroup.Prepend>
                        <InputGroup.Text>TASA DE CAMBIO</InputGroup.Text>
                        <InputGroup.Text><i className="fa fa-usd"></i></InputGroup.Text>
                    </InputGroup.Prepend>
                    <InputNumber min={0} value={valor} setData={this.setState.bind(this)} update='valor' />
                    <InputGroup.Append>
                        <Button variant='primary' onClick={this.actualizar.bind(this)}>
                            <i className="fa fa-refresh"></i> ACTUALIZAR
                        </Button>
                    </InputGroup.Append>
                </InputGroup>
                <Col md="4" style={{ fontSize: '12px' }}>
                    <strong>ultima atualizaci√≥n:</strong> {formatoFecha(fecha)}<br />
                    <strong>Hora:</strong> {formatoHora(fecha)}
                </Col>
            </Row>
            <Row>
                <Area
                    label={['FECHA Y HORA', 'PRECIO']}
                    values={this.get()}
                    title="FLUCTUACIONES DEL DOLAR"
                    hAxisTitle="FECHA Y HORA"
                />
            </Row>
        </>
    }
    actualizar() {
        const con = ConConfig('');
        const { valor } = this.state;
        con.post('/api/Dolares/set', { valor }, () => { this.send() })
    }
    get() {
        return this.state.dolares.map(d => [formatoFecha(d.fecha) + ' ' + formatoHora(d.fecha), d.valor])
    }
    async send() {
        try {
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/api/Dolares/formatter/last', { headers: { 'Authorization': 'Bearer ' + token } });
            let dolares: dolar[] = await response.json();
            let valor = 0;
            let fecha = '1999-01-01';
            dolares.forEach(d => {
                if (fechaToNumber(d.fecha) > fechaToNumber(fecha)) {
                    valor = d.valor;
                    fecha = d.fecha;
                }
            })
            this.setState({ dolares: dolares.sort((a, b) => fechaToNumber(a.fecha) - fechaToNumber(b.fecha)), valor, fecha });
        } catch (error) {
            console.log(error);
            this.setState({ dolares: [] });
        }
    }
    componentDidMount() { this.send() }
}